import React, {useState} from "react";

import {Container, Col, Row, Button, Carousel, OverlayTrigger, Popover} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import styled from 'styled-components'

import {CopyToClipboard} from 'react-copy-to-clipboard'

import photoHomePage from './project1/homePage.PNG'
import photoAdminBoardChart from './project1/AdminBoardChart.PNG'
import photoAdminBoardTable from './project1/AdminBoardTable.PNG'
import photoOrder from './project1/Order.PNG'
import photoProduct from './project1/Product.PNG'
import {FaRegCopy} from "react-icons/fa";
import {HashLink} from "react-router-hash-link";

const size = {
    large: '992px'
}

const Styles = styled.div`
--font-color: #841ACF;

    * {
      color: var(--font-color);
    }
    
    .container h4 {
      color: #B82FCA;
    }

    #back-btn {
      margin: 30px 0px 30px auto;
      width: 100px;
      background-color: #750E8A;
      border: 1px solid #750E8A;
      
      a {
      color: white;
      }
      
      a:hover {
        text-decoration: none;
      }
    }
      
    #back-btn:hover {
      opacity: 0.7;
    }
    
    #title-row {
      display: flex;
      justify-content: center;
      border-bottom: 3px solid var(--font-color);
    }
    
    #table-row {
      display: flex;
    }
    
    #date-technologies-col {
      border-right: 3px solid var(--font-color);
      margin-top: 5px;
      width: 30%;
    }
    
    #date-row {
      border-bottom: 3px solid var(--font-color);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-right: 5px;
      padding-bottom: 10px;
    }
    
    #technologies-row {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 5px 5px 0 0;
    }
    
    #description-col {
      width: 60%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 5px;
      
      h4{
        margin: 0 20px;
      }
    }
    
    #source-code-row {
      margin: 30px 0px;
      width: 100%;
    }
    #source-code-col {
      padding: 0px;
      
      .btn {
        background-color: #750E8A;
        border: 1px solid #750E8A;
        width: 150px;
        color: white;
        float: right;
      }
      
      .btn:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }
    
    .carousel-control-prev-icon {
       background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E") !important;
    }

    .carousel-control-next-icon {
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E") !important;
    }
    
    .carousel-caption {
      h1 {
        font-size: 4vw;
      }
      h4 {
        font-size: 2vw;
      }
      h1, h4 { 
        -webkit-text-stroke: 1px black;
        -webkit-text-fill-color: white;
      }
    }
    
    @media (max-width: ${size.large}) { 
      #table-row {
        flex-direction: column;
      }
      #date-technologies-col {
        width: 100%;
        border: none;
      }
      #technologies-row {
        border-bottom: 3px solid var(--font-color);
      }
      #description-col {
        width: 100%;
      }
  }
`;

const PopoverStyles = styled.div`

  .popover-body {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .copy-button {
    float: right;
    margin-right: 10px;
    margin-bottom: 10px;
  }
`;

const Project = () => {
    const [copySuccess, setCopySuccess] = useState("");
    const project1Link = "https://github.com/dgwiazda/rental";

    const copyMessage = () => {
        setCopySuccess("Copied!");
        setTimeout(() => {
            setCopySuccess('');
        }, 3000);
    };

    const popover = (
        <Popover id="popover">
            <PopoverStyles>
                <Popover.Title as="h3">My Facebook</Popover.Title>
                <Popover.Content>
                    {project1Link}
                </Popover.Content>
                <div className="copy-button">
                    {copySuccess}
                    <CopyToClipboard text={project1Link}>
                        <Button variant="outline-secondary" onClick={copyMessage}><FaRegCopy/></Button>
                    </CopyToClipboard>
                </div>
            </PopoverStyles>
        </Popover>
    );


    return (
        <Styles>
            <Container id="top-content">
                <Row>
                    <Button id="back-btn"><HashLink to="/#my-projects">Back</HashLink></Button>
                </Row>
                <Row id="title-row">
                    <h1>Hehe</h1>
                </Row>
                <Row id="table-row">
                    <div id="date-technologies-col">
                        <Row id="date-row">
                            <h1>Date</h1>
                            <h4>October 2020 - December 2020</h4>
                        </Row>
                        <Row id="technologies-row">
                            <h1>Technologies</h1>
                            <ul>
                                <li><h4>Java</h4></li>
                            </ul>
                            <ul>
                                <li><h4>Spring Boot</h4></li>
                            </ul>
                            <ul>
                                <li><h4>React.js</h4></li>
                            </ul>
                            <ul>
                                <li><h4>Bootstrap</h4></li>
                            </ul>
                        </Row>
                    </div>
                    <div id="description-col">
                        <h1>Description</h1>
                        <h4>In Hehe appliaction we have 3 different roles: guest, user, admin.
                            As a guest You are able to register, login, check products and their prices.
                            As a user We can also place an order, change user details and cancel bookings
                            and Admin can set product unavailiable and check statistics. If your role doesn't
                            allow you to do something, you will get appropriate message.<br/><br/> In frontend I
                            have used: React + Bootstrap + redux + router + axios + some other libraries.<br/> Backend
                            created with: Spring + Maven + MSSQL.</h4>
                    </div>
                </Row>
                <Row id="source-code-row">
                    <Col xs={9}/>
                    <Col id="source-code-col">
                        <OverlayTrigger trigger="click" placement="bottom"
                                        overlay={popover}>
                            <Button variant="info">Source code</Button>
                        </OverlayTrigger>
                    </Col>
                </Row>
            </Container>
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={photoHomePage}
                        alt="Home page"
                    />
                    <Carousel.Caption>
                        <h1>Home page</h1>
                        <h4>We can now go to login, contact and products page.</h4>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={photoProduct}
                        alt="Product page"
                    />

                    <Carousel.Caption>
                        <h1>Product page</h1>
                        <h4>We can check there choosen product price and go to Order Page.</h4>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={photoOrder}
                        alt="Order page"
                    />

                    <Carousel.Caption>
                        <h1>Order page</h1>
                        <h4>There we can choose date-range, quantity and
                            in some products more options and see how much will it cost.
                            Button will be disabled, if in choosen date there's no single product availiable.</h4>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={photoAdminBoardTable}
                        alt="Admin table statistics"
                    />

                    <Carousel.Caption>
                        <h1>Admin board - table</h1>
                        <h4>Admin is allowed to check statistics such as orders, products and users.</h4>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={photoAdminBoardChart}
                        alt="Admin chart statistics"
                    />

                    <Carousel.Caption>
                        <h1>Admin board - chart</h1>
                        <h4>Admin can also check monthly and annual sales on chart.</h4>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </Styles>

    )
}

export default Project;
