import React from "react";

import { HashRouter, Route } from "react-router-dom";

import App from "./App";
import Project from "./Project";


const Main = () => {
    return(
        <HashRouter basename="/">
            <Route exact path="/" component={App} />
            <Route path="/project/hehe" component={Project} />
        </HashRouter>
    )
}

export default Main;